
import {memo, useState} from 'react'

export const Fav = memo(({favs, setFavs}) => {
    const [state, setstate] = useState(false)
    const [modal, setModal] = useState('')
    const handleModal =(img)=>{
        setstate(true)
        
        setModal(img)
    }

    return (
        <div className="row">
            
            <a href="#favoritos"><button className="boton-fav btn btn-success">{favs.length} <i className="far fa-star" ></i></button></a>
            {
                favs.map(imagen=> 
                <div className="col-3" id="favoritos" key={imagen} >
                    
                    <img src={imagen} value={imagen} alt={imagen} className='img-fav mb-2' onClick={()=>handleModal(imagen)} />
                    
                </div>)
                
            }
            {
                state?
                <Modal modal={modal} setstate={setstate} favs={favs} setFavs={setFavs}/>
                :null
            }
            
        </div>
    )
})





 const Modal=({setstate, modal, setFavs, favs})=>{
    
    const deleteFav=()=>{
        const newArray=favs.filter(imgs=>imgs!==modal)
        setFavs(newArray)
        setstate(false)
    }
    
    const offModal=()=>{
        setstate(false)
    }
    
    return(
        <>
        
            
                
                <div className="modal-dialog ventana" >
                <div className="modal-content">
                    <div className="row">
                            <div className="btn-group" role="group">
                            <button className="btn " onClick={deleteFav}>Delete <i className="far fa-star" ></i></button>
                            <button className="btn-danger btn " onClick={offModal}>X</button>

                            </div>

                    </div>
                   
                           <img src={modal} alt={modal} width="100%"  height="300px" /> 
                   
                </div>
                </div>
                
            
                
                


        
        </>
    )
 }