import { memo } from 'react';
import './index.css'
const Carta = memo(({message, state, favoritos}) => {
    
    
    
    
    
    return(
    
            <>

            {               
                
                message.map(imagen=> 

                    <div className="col-md-6 col-sm-12 card mb-5 mt-3 align-items-center" key={imagen}>
                    
                    
                    <img className="card-img imagen" src={imagen} alt={state}/>
     
                    <button className="btn btn-danger fuente " 
                            onClick={()=>favoritos(imagen)}>FAV <i className="far fa-star"></i></button>
                    
                            
                            
                            </div>
                            )
                            
                        }
            
                </>

    )})
 
export default Carta;