import React from 'react'
import { useFetch } from './useHooks/useFetch'

export const SelectRazas = React.memo (() => {
    
    const razas = 'https://dog.ceo/api/breeds/list/all'
    const{datos}= useFetch(razas)
    
    const {message} = datos
    
    
    
    return (
        <>
        {message
        ?
        Object.keys(message).map(raza=>
           <option className="text-center fw-bold" value={raza} key={raza}>{raza}</option>
        )
       :
        
       'cargando'}
        </>
        )
})
