import React, {useState} from 'react'
import { Input } from './components/Input';
import  Peticion from "./components/Peticion";
import { Header } from './components/Header';
 
import './components/index.css'
import Footer from './components/Footer';

function App() {

    
  const [state, setState] = useState('')
  
  const razaInput=(valor)=>{
    if(valor.trim().length>2)
    setState(valor)
  }


  return (

    <>
      <Header/>
      <div className='container fondo'>
    
      <h5 className=" text-center  fw-bold listado mt-2">Listado de razas </h5>
      <section className="d-flex justify-content-center mb-5 m-1 col-12 ">
      <Input razaInput={razaInput}/>
        
      </section>
      
    
      <Peticion state={state}/>
      <div>
        <Footer/>
      </div>
    </div>
    </>
  );
}

export default App;
