import React, { useState, useEffect } from 'react'

import Carta from './Carta'
import { Fav } from './Fav'
import {useFetch} from './useHooks/useFetch'


const Peticion = ({state}) => {
    const [render, setRender] = useState('https://dog.ceo/api/breeds/image/random/6')
    const [favs, setFavs] = useState([])

   
    const {datos} = useFetch(render)
    const {message}= datos
   
    
    useEffect(() => {
        state.trim().length>2 && setRender(`https://dog.ceo/api/breed/${state}/images/random/4`)
       
    }, [state])
    
    

    //Agregar favoritos
    const favoritos=(e)=>{
        if(!favs.includes(e)){

            if(favs.length<8){
                setFavs([...favs, e])
            }else alert('Maximo de favoritos alcanzado')

        }
    }
    
    return ( 
        <div className="row justify-content-center text-light">
            <h4>
            
        <li className="mb-3">Presione cuadro para ver listado 👆</li>
        
        <li className="m-1 mb-4"><small> Puede elegir un maximo de 8 favoritos</small></li>
        {
            favs.length>0?
        <small> Presione imagen 👇 para editar Fav</small>
            :null
        }
      
      </h4>
            <div className="row">
                <div className="col-md-4 col-sm8">
                    <h2><i className="fas fa-dog"></i><span className="bg-dark text-white rounded fuente ">{state}</span></h2>
                </div>
                <div className="col-md-8 col-sm-12 caja-fav">
                    
                     
                    <Fav favs={favs} setFavs={setFavs}/>

                   
                    
                    
                </div>
            </div>
            
            <div className="row">
                <div className="col-12 ">
                    <div className="row b-3">
                        {
                            !message?
                            <p className='text-center text-warning'><span className="bg-dark p-3 rounded"> Cargando...</span></p>
                            :
                        
                            <Carta message={message} state={state} favoritos={favoritos}/>
                        }

                    </div>
                </div>


            </div>
            
        </div> 
        );
}
 
export default Peticion;