import React, {useState} from 'react'
import { SelectRazas } from './SelectRazas'

export const Input = ({razaInput}) => {
    
    const [state, setstate] = useState('')
    
    

    
    

    const  handleInput=(e)=>{
        setstate(e.target.value)
        
    }
    const handleSubmit=(e)=>{
        e.preventDefault()
        razaInput(state)
    }

    return (
        <>
        <form onSubmit={handleSubmit} className="input-group input " >
        <hr/>
        <select onChange={handleInput} className="form-control text-center">
            <option value="" key="vacio"></option>
              <SelectRazas />
            
        </select>
            <button type='submit' className="btn btn-warning">buscar</button>
        </form>
        
        
            
        
        </>
    )
}
